.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 70px;
  padding: 20px;
  color: white;
  font-family: 'Fondamento', cursive;
}

.App-title {
  font-size: 1.4em;
}

.App-intro {
  font-size: large;
}

.MainProduct{
  margin-bottom:50px;
  margin-left:16px;
  margin-right:16px;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background-color: bisque;
  border-radius:1px;
  width:300px; 
  text-align: center;
}

.ProductContent{
  margin-top: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  text-align:center;
  -ms-flex-pack: center;
      justify-content: center;
}
.ClockImagestyle{
  height:300px;
}
.ProductDetails{
  padding: 8px 10px;
  padding-bottom: 1px;
}
.ProductName {
  text-align: left;
  font-size: 12px;
  width: 200px;
  font-family: 'Fondamento', cursive;
}
.ProductName a{
  color:black;
}
.ProductName a:hover{
  color:black;
}

.ProductPrice{
  margin-top:10px;
  text-align: left;
}

.icon-heart-empty{
  padding: 9px 14px;
  color:brown;
}

.icon-shopping-cart{
  padding: 7px 10px;
  background-color: brown;
  color:white;
  border-style: solid;
  border-width:1px;
  border-color: brown;
}

.icon-shopping-cart:hover{
  background-color: white;
  color:brown;
  border-style: solid;
  border-width:1px;
}

.Price{
  margin-top:4px;
  font-family: 'Montserrat Subrayada', sans-serif;
  font-size: 1.3em;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



